.projects {
    width: 30%;
    height: 32vh;
    margin: 20px 10px;
    border: 2px solid #ececec;
    border-radius: 10px 10px 0 0;
    overflow: hidden;
}

.projects-browser {
    height: 20px;
    background-color: #d3d3d3;
    display: flex;
    align-items: center;
}

.projects-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: white;
    margin: 3px;
}

.projects-img {
    width: 100%;
}
.projects:hover {
    position:relative;
    /* top:-25px;
    left:-35px; */
    width:320px;
    /* height:auto; */
    display:block;
    z-index:999;
    box-shadow: 0 5px 5px rgba(0,0,0,0.3);
}